import React from 'react';

const data = [
    {key: 0, transfer: -1},
    {key: 50, transfer: 300000},
    {key: 100, transfer: 350000},
    {key: 150, transfer: 400000},
    {key: 200, transfer: 450000},
    {key: 250, transfer: 500000},
    {key: 300, transfer: 550000},
    {key: 400, transfer: 600000},
    {key: 500, transfer: 650000},
    {key: 600, transfer: 700000},
    {key: 700, transfer: 750000},
    {key: 1000, transfer: 800000},
    {key: 1500, transfer: 900000},
    {key: 2000, transfer: 1000000},
    {key: 2500, transfer: 1100000},
    {key: 3000, transfer: 1200000},
    {key: 3500, transfer: 1300000},
    {key: 4000, transfer: 1400000},
    {key: 4500, transfer: 1500000},
    {key: 5000, transfer: 1600000},
    {key: 10000, transfer: 2000000},
    {key: Infinity, transfer: 2500000},
];
const UaeShipping = (p) => {
    let result;

    for (let i = 1, len = data.length; i < len; i++) {
        if (p < data[i].key && p >= data[i - 1].key) {
            result = data[i].transfer;
            break;
        }
    }
    return result;
};

export default UaeShipping;